body {
  font-family: sans-serif;
 -webkit-font-smoothing: antialiased;
 -moz-osx-font-smoothing: grayscale;
 text-rendering: optimizeLegibility;
 font-smooth: always;
  margin: 0;
  padding: 0;
}

.word-cloud-outer {
 -ms-flex-align: center;
     align-items: center; 
 bottom: 0;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-direction: column;
     flex-direction: column;
 -ms-flex-pack: center;
     justify-content: center;
 left: 0;
 padding: 20px 0;
 position: absolute;
 right: 0;
 top: 200px;
 z-index: -1;
}

.word-cloud-inner {
 display: -ms-flexbox;
 display: flex;
 -ms-flex-direction: column;
     flex-direction: column;
 height: 100%;
 max-width: 1000px;
 width: 100%;
}

.tag-cloud {
 -ms-flex: 1 1;
     flex: 1 1;
}

.tag-cloud > div {
 -webkit-transition: 1.4s;
 -o-transition: 1.4s;
 transition: 1.4s;
}

.edit-form {
  font-family: sans-serif;
}


